import React from "react"
import Rodal from 'rodal';
// include styles
import 'rodal/lib/rodal.css';

class ModalDialog extends React.Component{
  constructor(props) {
    super(props);
  }

  hide(){
    this.props.toggleVisible.call()
  }
  render() {
    return (
      <div>
        <Rodal visible={this.props.show_modal} onClose={this.hide.bind(this)}>
          <div>Content</div>
        </Rodal>
      </div>
    );
  }
}

export default ModalDialog
